import { Canvas } from "canvaskit-wasm";
import { CanvaskitMap } from "./map";

export interface LayerOptions {
  /**
   * , ,  0
   */
  zIndex?: number;

  /**
   * 
   */
  hidden?: boolean;
}

/**
 *  Layer  class
 */
export abstract class Layer<O extends LayerOptions = LayerOptions> {
  /**
   * addLayer  constructor 
   */
  map?: CanvaskitMap;
  constructor(public _options: O) {}

  /**
   *  Layer 
   */
  abstract draw(canvas: Canvas): void;

  /**
   *  init 
   */
  _initialized = false;

  /**
   *  addLayer 
   *
   *  this.map
   */
  async init() {}

  /**
   * removeLayer 
   */
  dispose() {}

  get zIndex() {
    return this._options.zIndex ?? 0;
  }

  get options() {
    return this._options;
  }

  /**
   *  options 
   * get options() 
   */
  set options(options: O) {
    this._options = options;
  }

  get canvaskit() {
    return this.map?.canvaskit;
  }
}
